.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: hsl(26deg, 100%, 55%);
  color: hsl(0deg, 0%, 100%);
  text-align: center;
  border-radius: 0.625rem;
  font-size: 1.125rem;
  padding: 1.25rem;
}
.btn:hover, .btn:active {
  box-shadow: 1px 1px 4px hsl(26deg, 100%, 55%);
}